//   &::after {
//     content: "";
//     position: absolute;
//     top: -0.3vw;
//     left: 0.8vw;
//     width: 0.8vw;
//     height: 0.8vw;
//     transform: rotate(45deg);
//     background-color: #2f2f2f;
//   }
// `;
export default Introduce;
